import './payCheck.scss';
import backArrow from '../images/arrow.svg'

import { NavLink } from "react-router-dom";

import {  Pagination } from 'swiper';
import  {Swiper,SwiperSlide}  from 'swiper/react';

//   
function PayCheck() {
    let slides = [];
    let list = [];
    for (let k=0; k<=10; k++){
        list.push(
            <div key={`content-${k}`} className="check__middle-item">
            <h3> Yellow Tair Moscato 
 0.75  7.6%</h3>
<p>800.00</p>
        </div>
        )
    }
    for (let i = 0; i<3; i++){
        slides.push(
            <SwiperSlide key={`slide-${i}`}>

            <div className="check__content">
                <div className="check__top"><h3> 1</h3></div>
                <div className="check__middle">
                
                {list}
                  
                </div>
            </div>
                    </SwiperSlide>
        )
      
    }


  return (
      <>
     


<div className="check">

    <div className="backPage"><NavLink to="/"><img src={backArrow} alt="" /></NavLink> </div>
<div className="check__sum">
    <h2>2 380.50</h2>
    <p> /</p>
</div>

<Swiper

    modules={[Pagination]}

   spaceBetween={20}
   centeredSlides={true}
    pagination={{ clickable: true }}

 
  >
 {slides}

  </Swiper>

<NavLink className="button-href" to="/tea"><button className="check__button">  </button></NavLink> 




</div>
</>
  );
}

export default PayCheck;